import { FC, useState } from "react"
import { EV_REPORT_PROP } from "../../EventReport/type/event-report-type"
import { useAppDispatch } from "@/app/lib/hooks/redux"
import { eventReportActions } from "../../EventReport"


export const Comment: FC = () => {
    const [isRequiredError, setIsRequiredError] = useState(false)

    const handleOnNameFocus = () => {
        if (isRequiredError) {
            setIsRequiredError(false)
        }


    }

    const dispatch = useAppDispatch();
    const handleChange = (value: string) => {
        dispatch(
            eventReportActions
                .setReportProp(
                    {
                        propName: EV_REPORT_PROP.COMMENT,
                        value
                    }
                )
        )
    }

    return (
        <>
            <label
                htmlFor="description-string-input"
                className="col-md-12 col-form-label"
            >
                Опишите кратко что было
            </label>
            <div className="col-md-12 mt-2  mb-2">
                <textarea
                    onChange={
                        (e) => handleChange(
                            e.target.value
                        )}
                    onFocus={handleOnNameFocus}
                    className="form-control"
                    // type="text"
                    // defaultValue="2019-08-19"
                    id="description-string-input"
                    lang="rus"
                    rows={8}
                />
            </div>
        </>
    )
}
