import React, { FC, useState } from "react"
import * as classes from '../../Report.module.scss'
import { Col, Container, Row, } from "reactstrap"
import { EV_REPORT_PROP, EventReportSelectItem, EventReportStateReport } from "@/entities/EventReport/type/event-report-type"
import ASelect from "@/shared/ui/Select/ASelect"


interface NoresultProps {
    report: EventReportStateReport
    handleChange: (type: EV_REPORT_PROP, value: string) => void
}

const Noresult: FC<NoresultProps> = ({ report, handleChange }) => {
    const noresult = report[EV_REPORT_PROP.NORESULT_REASON]
    const items = noresult.items
    const current = noresult.current

    return <React.Fragment>
        <div className="page-content">
            <Container fluid={true} >
                <Row>
                    {/* <div

                    className="col-md-12 col-form-label"
                > */}
                    <h3 className="text__bold col-md-12 col-form-label">Нерезультативный звонок</h3>
                    {/* </div> */}
                </Row>
                <Row className="">
                    <Col className={classes.status} sm={12}>
                        {
                            <ASelect
                                label={'Причины нерезультативности/ Переноса'}
                                nameForHandler={EV_REPORT_PROP.NORESULT_REASON}
                                handleChange={handleChange}
                                current={current}
                                items={items}
                            />
                        }
                    </Col>
                </Row>
            </Container>

        </div>
    </React.Fragment >
}

export default Noresult;