import React, { FC } from "react"
import * as classes from './EventItem.module.scss'
import { Button, Col, Container, Row, } from "reactstrap"
import { ReportContainer } from "@/entities/EventReport"
import { EventItemResultType } from "../model/EventItemSlice"
import { Presentation } from "@/entities/EventPresentation"
import { Plan } from "@/entities/EventPlan"
import { Comment } from "@/entities/EventComment"
import Header from "./components/Header"

interface EventItemProps {
    menuType: EventItemResultType,
    withPlan: boolean,
    // setCurentEventProp: (
    //     name: EVENT_PROPS,
    //     value: BXUser | null
    // ) => void

    //todo
    // send
    //cancelResultMenu
}




const EventItem: FC<EventItemProps> = ({
    menuType,
    withPlan,
    // setCurentEventProp,
    // send,
    // cancelResultMenu
}) => {


    return <React.Fragment>
        <Header />

        <div className={`page-content ${classes.wrapper}`}>
            <Container fluid={true} >
                {/* <Row>
                    <div

                        className="col-md-12 col-form-label"
                    ></div>
                </Row> */}

                <Row className="p-0">

                    <Col sm={6} className="p-0">
                        <ReportContainer
                            isNoResult={false}
                        />
                    </Col>
                    <Col sm={6} className="p-0">



                        {menuType === 'result'
                            ? <Presentation />
                            : <ReportContainer isNoResult={true} />

                        }

                    </Col>


                </Row>
                <Row className="mt-2">
                    <Col sm={6}>
                        {/* <h3 className={'bold'}> История Работы</h3> */}
                    </Col>
                    <Col sm={6}>
                        {/* <h3 className={'bold'}> Информация о клиенте</h3> */}
                    </Col>
                </Row>
                <Row className="mt-2">
                    <Col sm={6}>
                        <h3 className={'bold'}> Комментарий</h3>
                    </Col>
                    <Col sm={6}>
                        <h3 className={'bold'}> Планирование следующего звонка</h3>
                    </Col>
                </Row>
                <Row>

                    {<Col sm={6}>
                        <Comment />


                    </Col>}
                    {withPlan &&
                        <Col sm={6}>
                            <Plan />

                        </Col>
                    }
                </Row>

            </Container>



        </div>
    </React.Fragment >
}

export default EventItem