import { FC, useEffect, useState } from "react"
import * as classes from './Plan.module.scss'
import { EVENT_PROP} from "@/processes/event/types/event-types"
import { EV_PLAN_PROP } from "../type/event-plan-type"
import { EV_PLAN_STATE_ITEM } from "../model/EventPlanSlice"
import { BXUser } from "@/app/types/bitrix/bitrix-type"
import ASelect from "@/shared/ui/Select/ASelect"
import { DUserRoleStateItem, DepartmentRoleStateItem } from "@/features/Departament/type/department-type"


interface PlanProp {
    // department: DepartmentRoleStateItem
    responsibile: DUserRoleStateItem
    createdBy: DUserRoleStateItem
    date: string
    name: string
    type: EV_PLAN_STATE_ITEM
    eventPropChange: (type: EVENT_PROP, value: BXUser) => void
    planPropChange: (type: EV_PLAN_PROP, value: string) => void

}

const Plan: FC<PlanProp> = ({
    responsibile,
    createdBy,
    date,
    name,
    type,
    eventPropChange,
    planPropChange,
}) => {

    // const [currentPlan, setCurrentPlan] = useState(plan)
    // const [currentRespons, setCurrentRespons] = useState(responsibile?.current)
    // const [currentCreated, setCurrentCreated] = useState(createdBy?.current)
    const [isRequiredError, setIsRequiredError] = useState(false)


    // useEffect(() => {
    //     setCurrentRespons(responsibile?.current)
    // }, [responsibile?.current])

    const handleOnNameFocus = () => {
        if (isRequiredError) {

            setIsRequiredError(false)
        }


    }


    //  <React.Fragment>
    return <>

        {/* <div className="page-content"> */}
        {/* <Container fluid={true} > */}
        {/* <Row>
                    <div

                        className="col-md-12 col-form-label"
                    ></div>
                </Row> */}
        {/* <Row className="">

                    <Col sm={12}> */}
        {/* <Col sm={8}> */}
        <label
            htmlFor="example-string-input"
            className={`col-md-12 col-form-label ${isRequiredError ? 'calling-name-label-error' : ''}`}

        >
            {!isRequiredError
                ? 'Название - что нужно сделать'
                : 'Заполните обязательное поле - Тема звонка'
            }
        </label>

        <div className="col-md-12 mt-2 mb-2">
            <input
                onChange={
                    (e) => planPropChange(
                        EV_PLAN_PROP.NAME,
                        e.target.value

                    )}

                onFocus={handleOnNameFocus}
                className={`mt-0 form-control ${isRequiredError ? 'is-invalid' : ''}`}
                type="text"
                // className={`form-control is-invalid`}
                // defaultValue="2019-08-19"
                id="example-string-input"
                lang="rus"
            />
        </div>


        <label
            htmlFor="example-datetime-local-input"
            className="col-md-12 col-form-label"
        >
            Дата и время звонка
        </label>
        <div className="col-md-12 mt-2   mb-2">
            <input
                className="form-control mt-0"
                type="datetime-local"
                defaultValue={date}
                id="example-datetime-local-input"
                lang="rus"
                onChange={(e) => planPropChange(EV_PLAN_PROP.DATE, e.target.value)}
            />
        </div>
        <ASelect
            label={'Тип звонка'}
            nameForHandler={EV_PLAN_PROP.TYPE}
            handleChange={planPropChange}
            current={type.current}
            items={type.items}
        />

        {/* </Col >


    </Row >
            </Container > */}

        {/* </div > */}

    </>
    // </React.Fragment >
}

export default Plan