import React, { FC, useState } from "react"
import { Col, Container, Row, Badge } from "reactstrap"
import classes from  './Presentation.module.scss'
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { PresentationCountProp, PresentationProp, eventPresentationActions } from "../model/PresSlice"



export const Presentation: FC = () => {

    // const [wasFixedPresentation, setWasFixedPresentation] = useState(false)
    //state
    const presentationState = useAppSelector(state => state.eventPresentation)
    const count = presentationState[PresentationProp.COUNT]
    const companyCount = count[PresentationCountProp.COMPANY]
    const currentCount = count[PresentationCountProp.SMART]
    const isPresentationDone = presentationState[PresentationProp.IS_PRESENTATION_DONE]
    const isUnplannedPresentation = presentationState[PresentationProp.IS_UNPLANNED_PRESENTATION]


    //dispatch
    const dispatch = useAppDispatch()
    const actions = eventPresentationActions
    const setPresProp = (name: PresentationProp, value: boolean) => {
        dispatch(
            actions.setPresentationProp(
                { name, value }
            )
        )
    }


    return < React.Fragment >
        <div className="page-content">
            <Container fluid={true} >

                <Row className="mb-12">

                    <Col sm={12}>
                        <h3 className="text__bold col-md-4 pb-0 col-form-label">Презентации</h3>
                    </Col>



                </Row>
                <Row>
                    <Col sm={12}>
                        <Row sm={12}
                            className="d-flex flex-row justify-content-start align-items-start"
                        >

                            <Col sm={6}
                                className="d-flex flex-row justify-content-start align-items-start"
                            >
                                {/* <div> */}
                                <p className="pt-0 col-md-12 col-form-label mt-0 d-flex flex-row
                                    justify-content-start align-items-center
                                    ">По компании: <span>{companyCount}   </span> </p>
                                {/* <p style={{ width: '120px' }} className="text__small col-md-3">По компании</p> */}

                                <p className="pt-0 col-md-12 col-form-label ml-5 mt-0 d-flex flex-row
                                    justify-content-start align-items-center
                                    ">Текущие: <span>{currentCount}   </span></p>

                                {/* </div> */}
                                {/* <div sm={12}>
                                    <div className="f col-md-12 mt-1">
                                        <input
                                            disabled
                                            style={{'height': '37.6px'}}
                                            className="form-control mt-0"
                                            type="text"
                                            // defaultValue="2019-08-19"
                                            id="example-string-input"
                                            lang="rus"
                                            value={presentation.companyCount}
                                        />
                                    </div>
                                </div> */}
                            </Col>
                            {/* <Col sm={6}
                                className="d-flex flex-column justify-content-start align-items-start"
                            >
                                <div sm={12}>
                                    <label className="col-md-12 col-form-label mt-2">Текущие</label>
                                  
                                </div>
                                <div sm={12}>
                                    <div className="f mt-1">
                                        <input
                                            disabled
                                            style={{ 'height': '37.6px' }}
                                            className="form-control mt-0"
                                            type="text"
                                            // defaultValue="2019-08-19"
                                            id="example-string-input"
                                            lang="rus"
                                            value={presentation.smartCout}
                                        />
                                    </div>
                                </div>


                            </Col> */}

                        </Row>

                        <div
                            onClick={() =>
                                setPresProp(
                                    PresentationProp.IS_PRESENTATION_DONE,
                                    !isPresentationDone
                                )}
                            className="d-flex flex-column justify-content-start align-items-start">
                            <label
                                htmlFor="example-string-input"
                                className="col-md-6 mt-1 col-form-label mt-3"
                            >
                                Зафиксировать презентацию
                            </label>
                            <div className="presentation__actions">
                                {/* <Badge
                                    style={{ cursor: 'pointer' }}
                                    className={"outline col-md-3 font-size-11 badge-soft-danger badge bg-danger "}
                                >
                                    {'Не было'}
                                </Badge> */}
                                <Badge
                                    style={{ cursor: 'pointer' }}
                                    outline
                                    // @ts-ignore
                                    color={false}
                                    className={`col-md-3 
                                    ${!isPresentationDone
                                            ? 'presentation__badge'
                                            : 'presentation__badge__active'
                                        }`}
                                >
                                    {'Была проведена'}
                                </Badge>
                                {isPresentationDone && <div
                                    className='presentation__done__count'
                                >
                                    +1
                                </div>}
                            </div>

                        </div>


                        {/* <div className="col-md-12 mb-2 calling-btn-wrapper"> */}


                        {/* <Button
                            disabled={!isFullComplete}
                            onClick={() => presentationDone()}
                            className="btn col-md-12 mb-2 calling-button call-button presentation" size='sm' >Проведена Презентация</Button> */}

                    </Col>
                </Row>
            </Container>


        </div>
    </React.Fragment >
}

